import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        leftMenu: '',
        settings: false,
        theme: localStorage.getItem("theme") || 'theme-dark-blue',
        primaryColor: localStorage.getItem("primary-color") || 'primary-green',
        listMenu: [
            {
                name: 'Home',
                path: '/',
            },
            {
                name: 'Core',
                path: '',
                sub: [
                    {
                        name: 'Users',
                        path: '/users'
                    }
                ]
            },
            {
                name: 'Mahasiswa',
                path: '',
                sub: [
                    {
                        name: 'StartUp',
                        path: '/startup'
                    },
                    {
                        name: 'Organization',
                        path: '/organization'
                    },
                     {
                        name: 'Forum',
                        path: '/forum'
                    },
                    {
                        name: 'Explore',
                        path: '/explore'
                    }
                ]
            },
            {
                name: 'Event',
                path: '',
                sub: [
                    {
                        name: 'New Event',
                        path: '/new/event'
                    },
                    {
                        name: 'Events',
                        path: '/event'
                    }
                ]
            },
            {
                name: 'Social action',
                path: '',
                sub: [
                    {
                        name: 'New Action',
                        path: '/new/action'
                    },
                    {
                        name: 'Social Actions',
                        path: '/action'
                    }
                ]
            }
        ]
    },
    mutations: {
        toggleLeftMenu(state, to) {
            state.leftMenu = to;
        },
        setTheme(state, to) {
            state.theme = to
            localStorage.setItem("theme", to)
        },
        setPrimaryColor(state,to){
            state.primaryColor = to
            localStorage.setItem("primary-color", to)
        },
        toggleSetting(state,to){
            if(state.settings){
                state.settings = false
            }else{
                state.settings = true
            }
        }
    },
    actions: {

    },
    modules: {}
})